





















































var _self, x, y;
export default {
	data() {

		return {
			storageData: {
				userId: 0,
				nickname: '',
				avatar: '',
				login: false
			},
			/* articles: [], */
			//2个按钮的宽度
			btn1Width: 70,
			btn2Width: 70,
			//2个按钮的总宽 = btn1Width + btn23Width【uni-app 模板内不建议复杂运算请手动配置】
			deleteBtnWidth: 140,
			//正在滑动的索引
			scrollIndex: -1,
			//正在删除的索引
			deleteIndex: -1,
			//模拟数据
			articles: [
				//img - 图片地址 title - 标题 desc - 描述 - status 读取状态
				{
					title: "",
					desc: '',
					status: "未读"
				},
				{
					title: "",
					desc: '',
					status: "未读"
				},
				{
					title: "",
					desc: '',
					status: "未读"
				}
			],
			recommend: true,
			special: false,
		};

	},
	onLoad: function() {
		uni.setNavigationBarTitle({
			title: '我的文章'
		});
		_self = this;
	},
	onShow: function() {
		var _this = this;
		const loginKey = uni.getStorageSync('login_key');
		if (loginKey) {
			this.storageData = {
				login: loginKey.login,
				nickname: loginKey.nickname,
				avatar: loginKey.avatar,
				userId: loginKey.userId
			};
			uni.request({
				url: this.apiServer + '/article/article_list',
				method: 'GET',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					uId: this.storageData.userId
				},
				success: res => {
					_this.articles = res.data.data;
				}
			});
		} else {
			this.storageData = {
				login: false
			};
		}
	},
	methods: {
		clickshow: function() {
			this.recommend = true;
			this.special = false;
		},
		clickshow2: function() {
			this.recommend = false;
			this.special = true;
		},
		gotoDetail: function(aId) {
			uni.navigateTo({
				url: '../article_detail/article_detail?aId=' + aId + '&userId=' + this.storageData.userId
			});
		},
		getArticle: function() {
			var _this = this;
			uni.request({
				url: this.apiServer + '/article/article_list',
				method: 'GET',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				data: {
					uId: uni.getStorageSync('login_key').userId
				},
				success: res => {
					_this.articles = res.data.data;
				}
			});
		},
		removeMsg: function(aId) {
			uni.request({
				url: this.apiServer + '/article/' + aId,
				method: 'DELETE',
				header: {
					'content-type': 'application/x-www-form-urlencoded'
				},
				success: res => {
					uni.showToast({
						title: '删除成功!',
						duration: 1500
					});
					 this.getArticle(); 
				}
				
			});

		},
		touchStart: function(e) {
			x = e.mp.changedTouches[0].clientX;
			y = e.mp.changedTouches[0].clientY;
		},
		touchEnd: function(e) {
			x = x - e.mp.changedTouches[0].clientX;
			y = y - e.mp.changedTouches[0].clientY;
			if (Math.abs(x) < 50) {
				return;
			}
			if (Math.abs(x) < Math.abs(y)) {
				return;
			}
			var index = e.currentTarget.dataset.id;
			if (index == this.scrollIndex) {
				return;
			}
			_self.scrollIndex = index;
		},
		changStatus: function(e) {
			var index = e.currentTarget.dataset.id;
			this.msg[index].status = "已读";
		},
		handleTime: function(date) {
			var d = new Date(date);
			var year = d.getFullYear();
			var month = d.getMonth() + 1;
			var day = d.getDate() < 10 ? '0' + d.getDate() : '' + d.getDate();
			var hour = d.getHours() < 10 ? '0' + d.getHours() : '' + d.getHours();
			var minutes = d.getMinutes() < 10 ? '0' + d.getMinutes() : '' + d.getMinutes();
			var seconds = d.getSeconds() < 10 ? '0' + d.getSeconds() : '' + d.getSeconds();
			return year + '-' + month + '-' + day + ' ' + hour + ':' + minutes + ':' + seconds;
		},
		gotoWrite: function() {
			uni.showModal({
				title: '提示',
				content: '现在要去编写文章吗？',
				cancelColor: '#EA6F5A',
				success: function(res) {
					if (res.confirm) {
						uni.navigateTo({
							url: '../write/write'
						});
					} else if (res.cancel) {
						console.log('用户点击取消')
					};
				}
			});

		},
	}
}
